import {
	mapState,
	mapActions,
	mapMutations
} from 'vuex'
import banners from '../../components/index/banner.vue'
import commonnav from '../../components/common/commonnav.vue'
import parametermodel from '../../components/gooddetail/parametermodel.vue'
import uParse from '../../components/jyf-parser/jyf-parser.vue'
import problemmodel from '../../components/gooddetail/problemmodel.vue'
import commongood from '../../components/common/commongood.vue'
import goodsNav from '../../components/gooddetail/goodsnav.vue'
// import uParse from '../../components/feng-parse/parse.vue'
export default {
	name: "",
	components: {
		banners,
		commonnav,
		parametermodel,
		uParse,
		problemmodel,
		commongood,
		goodsNav
	},
	props: {},
	data() {
		return {
			// 接收传过来的id值
			goodsId: null,
			// 接收openId
			openId: '',
		}
	},
	methods: {
		...mapActions(['getGoodsDetail', 'addCollects', 'getCartsNum']),
		...mapMutations(['setLoginStatus']),
		// 收藏按钮(子组件分发回父组件的方法)
		collect(e) {
			if (this.loginStatus) {
				this.addCollects({
					goodsId: this.goodsId,
					openId: this.openId
				})
				this.getGoodsDetail({
					id: this.goodsId,
					openId: this.openId
				})
				setTimeout(() => {
					if (this.goodsInfos.collected) {
						uni.showToast({
							title: '取消收藏成功'
						});
					} else {
						uni.showToast({
							title: '收藏成功'
						});
					}
				}, 800)
			} else {
				uni.showModal({
					title: '特别提醒',
					content: '检测到您还未登录,无法使用该程序的一些功能,推荐您登录后使用该程序',
					showCancel: false,
					confirmText: '确定',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		},
	},
	mounted() {
		if (this.goodsId !== null && this.openId !== '') {
			uni.showLoading({
				title: '加载中...',
			});
			this.getGoodsDetail({
				id: this.goodsId,
				openId: this.openId
			})
		}
	},
	onShow() {
		if (uni.getStorageSync('openId')) {
			this.openId = uni.getStorageSync('openId')
		}
		if (uni.getStorageSync('userInfo')) {
			this.setLoginStatus(true)
			this.getCartsNum(this.openId)
		}
	},
	onLoad(option) {
		this.goodsId = option.id
		// console.log(this.goodsId);
	},
	filters: {

	},
	computed: {
		...mapState(['goodsInfos', 'loginStatus', 'cartsArr']),
	},
	watch: {

	},
	directives: {

	}
}
